<script>
import {
	mapState
} from 'vuex'
export default {
	// eslint-disable-next-line vue/multi-word-component-names
	name: 'Home',
	data: () => ({
		toastShow: false,
		toastText: '',
		inviteAddress: '',
		isLoading: false,
	}),
	computed: {
		...mapState(['user', 'walletAddress', 'isConnected', 'isChainCorrect', "isRegistered", 'walletAddress', 'poolInstance']),
		walletAddressShow() {
			return this.walletAddress.substr(0, 5) + "..." + this.walletAddress.substr(-5)
		},
		rewardCount() {
			console.log(this.user)
			if (this.user) {
				console.log(this.user.tokenRewardCount)
				return this.user.tokenRewardCount || 0;
			} else {
				return 0;
			}
		},
		inviteLink() {
			if (!this.walletAddress) return "";
			return window.location.origin + window.location.pathname +"#/?address=" + this.walletAddress;
		},
		teamCount() {
			if (this.user) {
				return this.user.downlines && this.user.downlines.length ? this.user.downlines.length : 0;
			} else {
				return 0;
			}
		},
		downLines() {
			if (this.user) {
				return this.user.downlines || [];
			} else {
				return 0;
			}
		}
	},
	created() {
		let address = this.$getQueryString("address");
		if (address) {
			this.inviteAddress = address;
		}
	},
	methods: {
		async copyLink() {
			await navigator.clipboard.writeText(this.inviteLink);
			// this.toastText = this.$t('common.copySuccess');
			this.showToast("复制成功");
		},
		showToast(text) {
			this.toastText = text;
			this.toastShow = true;
			setTimeout(() => {
				this.toastShow = false;
				this.toastText = "";
			}, 2000);
		}
	}
};
</script>

